import RecommendPage from "@/components/RecommendPage.vue";
import HotPage from "@/components/HotPage.vue";
import ManPage from "@/components/ManPage.vue";
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
import { mapState, mapMutations } from "vuex";
export default {
  name: "HomeView",
  computed: { ...mapState(["scrollchange", "collectionid"])
  },
  watch: {
    unlikeid: function (newunlike) {
      localStorage.setItem("unlikeid", JSON.stringify(newunlike));
    }
  },
  methods: { ...mapMutations(["setcollection"]),
    changetabswiper: function () {
      this.onetab = true;
      this.twotab = false;
    },
    onSelect: function (index) {
      if (index.name == "加入书架稍后再看") {
        if (this.collectionid.includes(parseInt(this.pullcomic.id))) {
          Toast({
            message: '已加入书架',
            position: 'bottom'
          });
        } else {
          this.setcollection({
            msg: this.pullcomic
          });
          Toast({
            message: '添加成功',
            position: 'bottom'
          });
        }
      } else if (index.name == "不感兴趣") {
        this.unlikeid = [this.pullcomic.id, ...this.unlikeid.filter(o => o != this.pullcomic.id)];
      }
    },
    changeasshow: function ($event) {
      this.axios.get("https://apis.netstart.cn/bcomic/ComicDetail", {
        params: {
          comicId: $event.item_id
        }
      }).then(res => {
        this.show = true;
        this.pullcomic = res.data.data;
      });
    },
    removeunlike: function ($event) {
      this.unlikeid = [...this.unlikeid.filter(o => o != $event.item_id)];
    },
    change: function () {
      this.changemod = true;
    },

    onChange(title) {
      this.showindex = title;
      this.tabindex = title;

      if (this.tabindex == 2 || this.tabindex == 3) {
        this.onetab = false;
        this.twotab = true;
      } else {
        this.onetab = true;
      }
    },

    onScroll({
      scrollTop
    }) {
      if (scrollTop > 10) {
        this.change();
      } else {
        this.changemod = false;
      }
    }

  },
  data: function () {
    return {
      twotab: true,
      tabindex: 0,
      onetab: true,
      pullcomic: null,
      show: false,
      actions: [{
        name: '加入书架稍后再看'
      }, {
        name: '不感兴趣'
      }],
      active: 1,
      hotSearch: [],
      tabType: [],
      changemod: false,
      isTransition: true,
      index: 1,
      showindex: 1,
      timer: "",
      scrollnum: 31,
      unlikeid: JSON.parse(localStorage.getItem("unlikeid")) || []
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/SearchRecommend").then(res => {
      this.hotSearch = res.data.data;
    });
    this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageAllTabs").then(res => {
      this.tabType = res.data.data.home_type;
      this.tabType.splice(2, 0, ...res.data.data.human); // this.tabType.splice(6, 0, res.data.data.home_type[4]);

      this.tabType.splice(4, 1);
    });
  },

  mounted() {
    this.timer = setInterval(() => {
      if (this.index >= 2) {
        setTimeout(() => {
          this.isTransition = false, this.index = 0;
        }, 500);
      }

      this.isTransition = true;
      this.index++;
      this.scrollnum = this.$refs?.li[0]?.offsetHeight;
    }, 5000);
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },

  components: {
    RecommendPage,
    HotPage,
    ManPage
  }
};