import "core-js/modules/es.array.push.js";
import Vue from "vue";
import { Lazyload } from "vant";
import RecommendCard from "./RecommendCard.vue";
import { mapMutations } from "vuex";
Vue.use(Lazyload);
export default {
  props: {
    unlikeid: Array
  },
  data: function () {
    return {
      bannerlist: [],
      images: [],
      index: 0,
      pageindex: 1,
      recommendcomiclist: [],
      loading: false,
      finished: false
    };
  },
  methods: { ...mapMutations(["upscrollchange", "downscrollchange"]),

    onChange(index) {
      this.index = index;
    },

    onLoad() {
      this.loading = true;
      this.axios.get("https://apis.netstart.cn/bcomic/HomeFeed", {
        params: {
          pageNum: this.pageindex,
          pageSize: 20
        }
      }).then(res => {
        this.loading = false;
        this.recommendcomiclist.push(...res.data.data.feeds);
        this.pageindex++;
      });
    }

  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/Banner").then(res => {
      this.bannerlist = res.data.data;
      this.images = [...res.data.data.map(o => o.img + "@660w.webp")];
    });
  },

  components: {
    RecommendCard
  }
};