import ManPageList from "./ManPageList.vue";
export default {
  data: function () {
    return {
      GetClassPageLayout: [],
      activeName: 0
    };
  },
  props: {
    item: Object,
    twotab: Boolean
  },
  methods: {
    onChange: function (title) {
      if (title == 0 || title == this.GetClassPageLayout.length - 1) {
        this.$emit('changetabswiper');
      }
    }
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageLayout", {
      params: {
        tabId: this.item.id
      }
    }).then(res => {
      this.pageindex++;
      this.GetClassPageLayout = res.data.data.layout;
    });
  },

  components: {
    ManPageList
  }
};