export default {
  provide() {
    return {
      reload: this.reload //提供数据

    };
  },

  data() {
    return {
      isRouterAlive: true
    };
  },

  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(() => {
        this.isRouterAlive = true;
      });
    }

  }
};