export default {
  data: function () {
    return {
      aindex: 0,
      id: 7,
      listrank: [],
      list: [],
      imgList: [require("../assets/commen/digital-0.png"), require("../assets/commen/digital-1.png"), require("../assets/commen/digital-2.png"), require("../assets/commen/digital-3.png"), require("../assets/commen/digital-4.png"), require("../assets/commen/digital-5.png"), require("../assets/commen/digital-6.png"), require("../assets/commen/digital-7.png"), require("../assets/commen/digital-8.png"), require("../assets/commen/digital-9.png")]
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/ListRank").then(res => {
      this.listrank = res.data.data.list;
    });
    this.axios.get("https://apis.netstart.cn/bcomic/GetRankInfo", {
      params: {
        id: 7,
        offset: 0,
        subId: 0
      }
    }).then(res => {
      this.list = res.data.data.list;
    });
  },

  watch: {
    id: function (newid) {
      this.axios.get("https://apis.netstart.cn/bcomic/GetRankInfo", {
        params: {
          id: newid,
          offset: 0,
          subId: 0
        }
      }).then(res => {
        this.list = res.data.data.list;
      });
    }
  }
};