import "core-js/modules/es.array.push.js";
import ReviewsCard from '@/components/ReviewsCard.vue';
export default {
  data: function () {
    return {
      scorevalue: 0,
      comicmsg: null,
      ListReviewsshort: [],
      ListReviewslong: [],
      active: 0,
      loading: false,
      finished: false,
      finishedshort: false,
      finishedlong: false,
      pageNumshort: 1,
      pageNumlong: 1,
      type: 0,
      sort: 0,
      show: false,
      mydis: JSON.parse(localStorage.getItem("textvalue")) || []
    };
  },
  methods: {
    deletebtn: function (index) {
      // console.log(index);
      if (index.name == "修改") {
        this.$router.push({
          path: '/mydiscussview',
          query: {
            id: this.$route.query.id
          }
        });
      } else if (index.name == "删除") {
        this.mydis = [...this.mydis.filter(o => o.comicid != this.$route.query.id)];
        localStorage.setItem("textvalue", JSON.stringify(this.mydis));
      }
    },

    showPopup() {
      this.show = true;
    },

    changsort() {
      this.pageNumshort = 1;
      this.pageNumlong = 1;
      let index = 1;
      this.sort = this.sort == 0 ? 1 : 0;
      this.finished = false;
      this.finishedshort = false;
      this.finishedlong = false;
      this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
        params: {
          comicId: this.$route.query.id,
          pageNum: index,
          type: 0,
          sort: this.sort
        }
      }).then(res => {
        if (res.data.data.reviews.length < 15 && this.active == 0) {
          this.finishedshort = true;
          this.finished = this.finishedshort;
        }

        this.ListReviewsshort = res.data.data.reviews;
      });
      this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
        params: {
          comicId: this.$route.query.id,
          pageNum: index,
          type: 1,
          sort: this.sort
        }
      }).then(res => {
        if (res.data.data.reviews.length < 15 && this.active == 1) {
          this.finishedlong = true;
          this.finished = this.finishedlong;
        }

        this.ListReviewslong = res.data.data.reviews;
      });
    },

    onchange() {
      this.finished = this.active == 0 ? this.finishedshort : this.finishedlong;
    },

    onLoad() {
      let index = this.active == 0 ? ++this.pageNumshort : ++this.pageNumlong;
      this.loading = true;
      this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
        params: {
          comicId: this.$route.query.id,
          pageNum: index,
          type: this.active,
          sort: this.sort
        }
      }).then(res => {
        this.loading = false;

        if (this.active == 0) {
          if (res.data.data.reviews.length < 15) {
            this.finishedshort = true;
            this.finished = this.finishedshort;
          }

          this.ListReviewsshort.push(...res.data.data.reviews);
        } else {
          if (res.data.data.reviews.length < 15) {
            this.finishedlong = true;
            this.finished = this.finishedlong;
          }

          this.ListReviewslong.push(...res.data.data.reviews);
        }
      });
    }

  },

  created() {
    Promise.all([this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
      params: {
        comicId: this.$route.query.id,
        pageNum: 1,
        type: 0,
        sort: this.sort
      }
    }), this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
      params: {
        comicId: this.$route.query.id,
        pageNum: 1,
        type: 1,
        sort: this.sort
      }
    })]).then(([a1, a2]) => {
      if (a1.data.data.reviews.length < 15 && this.active == 0) {
        this.finishedshort = true;
        this.finished = this.finishedshort;
      }

      this.ListReviewsshort = a1.data.data.reviews;

      if (a2.data.data.reviews.length < 15 && this.active == 0) {
        this.finishedlong = true;
        this.finished = this.finishedlong;
      }

      this.ListReviewslong = a2.data.data.reviews;
    });
    this.axios.get("https://apis.netstart.cn/bcomic/GetReviewDetailByComicID", {
      params: {
        comicId: this.$route.query.id
      }
    }).then(res => {
      this.comicmsg = res.data.data;
      this.scorevalue = res.data.data?.score.toFixed(1) / 2;
    });
  },

  components: {
    ReviewsCard
  }
};