import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    collectionid: JSON.parse(localStorage.getItem("collectionid")) || [],
    collection: JSON.parse(localStorage.getItem("collection")) || [],
    idnex: 1,
    scrollchange: false,
    historylist: JSON.parse(localStorage.getItem("historylist")) || [],
    historylisttime: JSON.parse(localStorage.getItem("historylisttime")) || [],
    comicpagehislist: JSON.parse(localStorage.getItem("comicpagehislist")) || [],
    deletelist: [],
    likelist: JSON.parse(localStorage.getItem("likelist")) || [],
    likelistre: JSON.parse(localStorage.getItem("likelistre")) || [],
    unlikelistre: JSON.parse(localStorage.getItem("unlikelistre")) || [],
  },
  getters: {
  },
  mutations: {
    provide() {
      return {
        reload: this.reload
      }
    },
    data() {
      return {
        isRouterAlive: true
      }
    },
    setlike(state, { msg }) {
      state.likelist = [msg.id, ...state.likelist.filter(o => o != msg.id)]
      localStorage.setItem("likelist", JSON.stringify(state.likelist));
    },
    removelike(state, { msg }) {
      state.likelist = [...state.likelist.filter(o => o != msg.id)]
      localStorage.setItem("likelist", JSON.stringify(state.likelist));
    },
    setlikere(state, { msg }) {
      state.likelistre = [msg.rpid, ...state.likelistre.filter(o => o != msg.rpid)]
      state.unlikelistre = state.unlikelistre.filter((v) => state.likelistre.some((val) => val != v))
      // console.log(state.unlikelistre.filter((v) => state.likelistre.some((val) => val != v)));
      localStorage.setItem("likelistre", JSON.stringify(state.likelistre));
      localStorage.setItem("unlikelistre", JSON.stringify(state.unlikelistre));
    },
    removelikere(state, { msg }) {
      state.likelistre = [...state.likelistre.filter(o => o != msg.rpid)]
      localStorage.setItem("likelistre", JSON.stringify(state.likelistre));
    },
    setunlikere(state, { msg }) {
      state.unlikelistre = [msg.rpid, ...state.unlikelistre.filter(o => o != msg.rpid)]
      state.likelistre = state.likelistre.filter((v) => state.unlikelistre.some((val) => val != v))
      // console.log(state.likelistre);
      localStorage.setItem("unlikelistre", JSON.stringify(state.unlikelistre));
      localStorage.setItem("likelistre", JSON.stringify(state.likelistre));
    },
    removeunlikere(state, { msg }) {
      state.unlikelistre = [...state.unlikelistre.filter(o => o != msg.rpid)]
      localStorage.setItem("unlikelistre", JSON.stringify(state.unlikelistre));
    },
    downscrollchange(state) {
      state.scrollchange = true
    },
    upscrollchange(state) {
      state.scrollchange = false
    },
    setcollection(state, { msg }) {
      state.collectionid = [msg.id, ...state.collectionid.filter(o => o != msg.id)]
      state.collection = [msg, ...state.collection.filter(o => o.id != msg.id)]
      localStorage.setItem("collectionid", JSON.stringify(state.collectionid));
      localStorage.setItem("collection", JSON.stringify(state.collection));
    },
    setcomicpagehislist(state, { item }) {
      state.comicpagehislist = [item, ...state.comicpagehislist.filter(o => o.id != item.id)]
      localStorage.setItem("comicpagehislist", JSON.stringify(state.comicpagehislist));
    },
    setHis(state, { comic, time }) {
      state.historylist = [comic, ...state.historylist.filter(o => o.id != comic.id)]
      state.historylisttime = [{ id: comic.id, time: time }, ...state.historylisttime.filter(o => o.id != comic.id)]
      localStorage.setItem("historylist", JSON.stringify(state.historylist));
      localStorage.setItem("historylisttime", JSON.stringify(state.historylisttime));
    },
    deletebtn: function (state) {
      let msg = state.collection.filter(o => state.deletelist.every(i => i != o.id));
      state.collectionid = [...msg.map(o => o.id)]
      state.collection = [...msg]
      localStorage.setItem("collectionid", JSON.stringify(state.collectionid));
      localStorage.setItem("collection", JSON.stringify(state.collection));
      state.deletelist = []
    },
    deletebtnhis: function (state) {
      let msg = state.historylist.filter(o => state.deletelist.every(i => i != o.id));
      state.historylist = [...msg]
      state.comicpagehislist = [...state.comicpagehislist.filter(o => state.deletelist.every(i => i != o.id))]
      localStorage.setItem("comicpagehislist", JSON.stringify(state.comicpagehislist));
      localStorage.setItem("historylist", JSON.stringify(state.historylist));
      state.deletelist = []
    },
    getdeletelist: function (state, id) {
      if (state.deletelist.some((o) => o == id)) {
        state.deletelist = state.deletelist.filter(o => o != id)
      } else {
        state.deletelist.push(id)
      }
    },
    allcheck: function (state) {
      state.deletelist = state.collection.map(o => o.id)
    },
    allcheckhis: function (state) {
      state.deletelist = state.historylist.map(o => o.id)
    },
    noallcheck: function (state) {
      state.deletelist = []
    },
  },
  actions: {
  },
  modules: {
  },
})
