import "core-js/modules/es.array.push.js";
export default {
  props: {
    item: Object
  },
  data: function () {
    return {
      pageindex: 1,
      list: [],
      loading: false,
      finished: false
    };
  },
  methods: {
    onLoad() {
      this.loading = true;
      this.axios.get("https://apis.netstart.cn/bcomic/GetHomeSecondaryComics", {
        params: {
          moduleId: this.item.id,
          pageNum: this.pageindex,
          pageSize: 20
        }
      }).then(res => {
        this.loading = false;
        this.pageindex++;
        this.list.push(...res.data.data.comics);
      });
    }

  }
};