import "core-js/modules/es.array.push.js";
import Vue from "vue";
import { Lazyload, Toast } from "vant";
import { mapMutations, mapState } from "vuex";
import LoadingPage from "../components/LoadingPage.vue";
Vue.use(Toast);
Vue.use(Lazyload);
export default {
  inject: ["reload"],
  computed: { ...mapState(["comicpagehislist"])
  },
  watch: {
    comicpagehislist: function () {
      this.loadingshow = true;
      this.imageList = [];
      this.pageid = this.comicpagehislist.filter(o => o.id == this.$route.query.id)[0];
      this.axios.get("https://apis.netstart.cn/bcomic/GetImageIndex", {
        params: {
          epId: this.pageid.pagemsg.id
        }
      }).then(res => {
        this.loadingshow = false;
        this.GetImageIndex = res.data.data.images;
        let imgsrc = [];
        let xindex = this.GetImageIndex.length < 10 ? this.GetImageIndex.length : 10;

        for (let x = 0; x < xindex; x++) {
          let src = `"https://manga.hdslb.com${this.GetImageIndex[x]?.path}@660w.webp"`;
          imgsrc = [...imgsrc, src];
        }

        this.getimgsrc(imgsrc);
      });
    }
  },
  methods: {
    noprev: function () {
      Toast({
        message: "没有上一话",
        position: "bottom"
      });
    },
    nonext: function () {
      Toast({
        message: "没有下一话",
        position: "bottom"
      });
    },
    prev: function () {
      for (var i = 0; i < this.ComicDetail.length; i++) {
        if (this.ComicDetail[i].id == this.pageid.pagemsg.id) {
          return this.ComicDetail[i - 1];
        }
      }
    },
    next: function () {
      for (var i = 0; i < this.ComicDetail.length; i++) {
        if (this.ComicDetail[i].id == this.pageid.pagemsg.id) {
          return this.ComicDetail[i + 1];
        }
      }
    },
    ...mapMutations(["setHis", "setcomicpagehislist"]),

    onClick() {
      this.controlpageshow = true;
    },

    onChange(index) {
      this.current = index - 1;

      if (index == this.imageList.length && this.imageList.length % 10 == 0 && this.japanloading == false) {
        this.japanloading = true;
        let imgsrc = [];
        let xindex = this.GetImageIndex.length % 10 !== 0 && this.GetImageIndex.length - this.imageList.length < 10 ? this.GetImageIndex.length % 10 : 10;

        for (let x = 0; x < xindex; x++) {
          let src = `"https://manga.hdslb.com${this.GetImageIndex[Math.floor(10 * index / 10) + x]?.path}@660w.webp"`;
          imgsrc = [...imgsrc, src];
        }

        this.getimgsrc(imgsrc);
        this.swiperindex = this.imageList.length;
      }
    },

    datecot: function () {
      let readtime = new Date().toString().split(" ");
      let readmonth = new Date().getMonth() + 1;
      let redehm = readtime[4].split(":")[0] + ":" + readtime[4].split(":")[1];
      return readtime[3] + "-" + readmonth + "-" + readtime[2] + " " + redehm;
    },
    getimgsrc: function (imgsrc) {
      let nowtime = new Date();

      if (nowtime.getTime() - this.gettime >= 300000) {
        this.gettime = nowtime.getSeconds();
      }

      this.axios.get(`https://apis.netstart.cn/bcomic/ImageToken?urls=[${imgsrc.join()}]&time='${this.gettime}'`).then(res => {
        this.japanloading = false; // this.$refs.read.scrollTo(0, 0);

        let ImageToken = res.data.data;

        for (let j = 0; j < ImageToken.length; j++) {
          this.imgurl = ImageToken[j].url + "?token=" + ImageToken[j].token;
          this.imageList.push(this.imgurl);
        }

        let createmsg = typeof this.pageid == "undefined" ? this.msg : this.pageid.pagemsg;
        createmsg?.unlock_type != 0 || createmsg?.is_locked ? this.show = true : this.show = false;
      });
    }
  },
  data: function () {
    return {
      contentlistshow: 0,
      show2: false,
      comicpageindex: 0,
      pageid: null,
      japanloading: false,
      pageindex: 0,
      controlpageshow: false,
      gettime: 0,
      imgurl: null,
      imageList: [],
      i: [],
      show: false,
      msg: null,
      comic: null,
      index: 0,
      current: 0,
      comicmsg: null,
      GetImageIndex: [],
      swiperindex: 1,
      ComicDetail: [],
      loadingshow: false
    };
  },

  created() {
    this.loadingshow = true;
    this.axios.get("https://apis.netstart.cn/bcomic/ComicDetail", {
      params: {
        comicId: this.$route.query.id
      }
    }).then(res => {
      this.loadingshow = false;
      this.pageid = this.comicpagehislist.filter(o => o.id == this.$route.query.id)[0];
      this.comicmsg = res.data.data;
      this.setHis({
        comic: this.comicmsg,
        time: this.datecot()
      });
      this.comic = res.data.data.ep_list;
      this.ComicDetail = res.data.data.ep_list.reverse();
      this.msg = this.ComicDetail[0];
      this.pageindex++;

      for (let x = 0; x < res.data.data.ep_list.length; x++) {
        if (res.data.data.ep_list[x]["id"] == this.$route.query.pageid) {
          this.msg = res.data.data.ep_list[x];
        }
      }

      let createmsg = typeof this.pageid == "undefined" ? this.msg : this.pageid?.pagemsg;
      let createcurrent = typeof this.pageid == "undefined" ? this.current : this.pageid?.current;

      for (var i = 0; i < this.ComicDetail.length; i++) {
        if (this.ComicDetail[i].id == this.pageid?.pagemsg.id) {
          this.comicpageindex = i;
        }
      }

      this.setcomicpagehislist({
        item: {
          id: this.$route.query.id,
          pagemsg: createmsg,
          current: createcurrent
        }
      });
    });
  },

  components: {
    LoadingPage
  }
};