import "core-js/modules/es.array.push.js";
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      index: 1
    };
  },

  methods: {
    onLoad() {
      this.loading = true;
      this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageSixComics", {
        params: {
          id: this.$route.query.id,
          pageNum: this.index++,
          pageSize: 15,
          isAll: 1
        }
      }).then(res => {
        this.list.push(...res.data.data.roll_six_comics);
        this.loading = false;
      });
    }

  }
};