import _debounce from "lodash/debounce";
import "core-js/modules/es.array.push.js";
export default {
  created() {
    this.getsreach = _debounce(this.getsuuget, 500);
    this.axios.get("https://apis.netstart.cn/bcomic/SearchRecommend", {
      params: {
        num: 6
      }
    }).then(res => {
      this.hotlist = res.data.data;
    });
  },

  data() {
    return {
      getsreach: null,
      value: "",
      hotlist: [],
      list: [],
      loading: false,
      finished: false,
      index: 1,
      keyword: "",
      sreachsuggestlist: [],
      suggestshow: true,
      hissearch: JSON.parse(localStorage.getItem("hissearch")) || [],
      showmaskb: false
    };
  },

  methods: {
    noshowmask: function () {
      this.showmaskb = false;
    },
    showmask: function () {
      this.showmaskb = true;
    },
    deletehis: function () {
      this.hissearch = [];
      this.showmaskb = false;
    },
    getstr: function (i) {
      return i.split('<em class="keyword">').join("").split("</em>").join("");
    },
    gethislist: function (i) {
      this.suggestshow = false;
      this.axios.get("https://apis.netstart.cn/bcomic/Search", {
        params: {
          styleId: -1,
          areaId: 2,
          isFinish: -1,
          order: -1,
          pageNum: 1,
          pageSize: 20,
          isFree: -1,
          keyWord: i
        }
      }).then(res => {
        this.keyword = i;
        this.hissearch = [...new Set([this.keyword, ...this.hissearch])];
        this.list = res.data.data.list;

        if (res.data.data.list.length <= 20) {
          this.finished = true;
        }
      });
    },
    getlist: function (i) {
      this.suggestshow = false;
      this.axios.get("https://apis.netstart.cn/bcomic/Search", {
        params: {
          styleId: -1,
          areaId: 2,
          isFinish: -1,
          order: -1,
          pageNum: 1,
          pageSize: 20,
          isFree: -1,
          keyWord: i.split('<em class="keyword">').join("").split("</em>").join("")
        }
      }).then(res => {
        this.keyword = i.split('<em class="keyword">').join("").split("</em>").join("");
        this.hissearch = [...new Set([this.keyword, ...this.hissearch])];
        this.list = res.data.data.list;

        if (res.data.data.list.length <= 20) {
          this.finished = true;
        }
      });
    },
    getsuuget: function (value) {
      if (value != "") {
        this.axios.get(" https://apis.netstart.cn/bcomic/SearchSug", {
          params: {
            term: value,
            num: 20
          }
        }).then(res => {
          this.sreachsuggestlist = res.data.data;
        });
      }
    },

    onInput(value) {
      this.suggestshow = true;
      this.getsreach(value);
    },

    onLoad() {
      this.axios.get("https://apis.netstart.cn/bcomic/Search", {
        params: {
          styleId: -1,
          areaId: 2,
          isFinish: -1,
          order: -1,
          pageNum: ++this.index,
          pageSize: 20,
          isFree: -1,
          keyWord: this.keyword
        }
      }).then(res => {
        this.list.push(...res.data.data.list);
      });
    },

    onSearch(key) {
      this.suggestshow = false;

      if (key != "") {
        this.axios.get("https://apis.netstart.cn/bcomic/Search", {
          params: {
            styleId: -1,
            areaId: 2,
            isFinish: -1,
            order: -1,
            pageNum: 1,
            pageSize: 20,
            isFree: -1,
            keyWord: key
          }
        }).then(res => {
          this.keyword = key;
          this.hissearch = [...new Set([this.keyword, ...this.hissearch])];
          this.list = res.data.data.list;

          if (res.data.data.list.length <= 20) {
            this.finished = true;
          }
        });
      }
    },

    onCancel() {
      this.$router.back();
    }

  },
  watch: {
    hissearch: function (newhis) {
      localStorage.setItem("hissearch", JSON.stringify(newhis));
    }
  }
};