import { Toast } from "vant";
import { mapMutations, mapState } from "vuex";
export default {
  inject: ['reload'],
  data: function () {
    return {
      loadingshow: false,
      contentlistshow: 0,
      msg: null,
      showjian: false,
      page: null,
      value: 0,
      page2: null,
      value2: 0,
      scorevalue: 0,
      show: false,
      list: null,
      comicmsg: null,
      active: 0,
      discusscount: 0,
      morecomics: [],
      showmorecomics: [],
      n: 1
    };
  },
  computed: { ...mapState(["collection", "collectionid", "scrollchange", "comicpagehislist", "likelist"]),
    watchcount: function () {
      return this.comicmsg?.watch / 10000 > 1 ? (this.comicmsg?.watch / 10000)?.toFixed(1) + "万" : this.comicmsg?.watch;
    }
  },
  methods: { ...mapMutations(["setcollection", "removecollection", "upscrollchange", "downscrollchange", "setcomicpagehislist", "setlike", "removelike"]),
    changemore: function () {
      this.showmorecomics = this.morecomics.recommend_comics.slice(this.n * 6, (this.n + 1) * 6);

      if (this.n < 2) {
        this.n++;
      } else {
        this.n = 0;
      }
    },

    onSelect(item) {
      this.show = false;
      Toast(item.name);
    }

  },

  created() {
    this.loadingshow = true;
    Promise.all([this.axios.get("https://apis.netstart.cn/bcomic/ComicDetail", {
      params: {
        comicId: this.$route.query.id
      }
    }), this.axios.get("https://apis.netstart.cn/bcomic/ListReviews", {
      params: {
        comicId: this.$route.query.id
      }
    }), this.axios.get("https://apis.netstart.cn/bcomic/GetReviewDetailByComicID", {
      params: {
        comicId: this.$route.query.id
      }
    }), this.axios.get("https://apis.netstart.cn/bcomic/ReplyMain", {
      params: {
        oid: this.$route.query.id
      }
    }), this.axios.get("https://apis.netstart.cn/bcomic/MoreRecommend", {
      params: {
        comicId: this.$route.query.id
      }
    })]).then(([a1, a2, a3, a4, a5]) => {
      this.msg = a1.data.data;
      this.list = a1.data.data.ep_list?.reverse();
      this.page = a2.data.data.reviews[0];
      this.page2 = a2.data.data.reviews[1];
      this.value = a2.data.data.reviews[0]?.score / 2;
      this.value2 = a2.data.data.reviews[1]?.score / 2;
      this.comicmsg = a3.data.data;
      this.scorevalue = a3.data.data?.score?.toFixed(1) / 2;
      this.discusscount = a4.data.data.cursor.all_count;
      this.morecomics = a5.data.data;
      this.showmorecomics = a5.data.data.recommend_comics.slice(0, 6);
      this.loadingshow = false;
    });
  }

};