import Type0Section from "./Type0Section.vue";
import Type3Section from "./Type3Section.vue";
import { mapMutations } from "vuex";
export default {
  methods: { ...mapMutations(["upscrollchange", "downscrollchange"])
  },
  data: function () {
    return {
      GetClassPageLayout: []
    };
  },
  props: {
    item: Object
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageLayout", {
      params: {
        tabId: this.item.id
      }
    }).then(res => {
      this.GetClassPageLayout = res.data.data.layout;
    });
  },

  components: {
    Type0Section,
    Type3Section
  }
};