import { mapState, mapMutations } from "vuex";
export default {
  computed: { ...mapState(["historylist", "historylisttime", "deletelist", "comicpagehislist"]),
    yesterdaytimelist: function () {
      let x = this.historylisttime.map(o => o.time.split(" ")[0].split("-")[2] < this.nowtime ? o : "");
      return x.filter(o => o != "");
    },
    yesterdaylist: function () {
      return this.historylist.filter(v => this.yesterdaytimelist.some(val => val["id"] == v["id"]));
    },
    todaylist: function () {
      return this.historylist.filter(v => this.yesterdaytimelist.every(val => val["id"] != v["id"]));
    },
    nowtime: function () {
      return new Date().getDate();
    }
  },
  props: {
    controllshow: Boolean
  },
  data: function () {
    return {};
  },
  methods: { ...mapMutations(["getdeletelist", "allcheck", "noallcheck"])
  },

  created() {}

};