import "core-js/modules/es.array.push.js";
export default {
  data: function () {
    return {
      styles: [],
      type: [],
      stylelist: {
        styleId: -1,
        areaId: -1,
        isFinish: -1,
        order: 0,
        pageNum: 1,
        pageSize: 39,
        isFree: -1
      },
      list: [],
      loading: false,
      finished: false,
      index: 1,
      topshow: false
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/AllLabel").then(res => {
      this.styles = res.data.data.styles;
      this.type = res.data.data;
    });
  },

  methods: {
    x: function () {
      let topheight = this.$refs.type.clientHeight + this.$refs.style.clientHeight + 41;

      if (this.$refs.typeview.scrollTop + 100 >= topheight) {
        this.topshow = true;
      } else {
        this.topshow = false;
      }
    },

    change(x) {
      this.index = 1;
      this.stylelist = { ...this.stylelist,
        ...x
      };
      this.axios.get("https://apis.netstart.cn/bcomic/ClassPage", {
        params: { ...this.stylelist,
          ...x
        }
      }).then(res => {
        this.list = res.data.data;
      });
    },

    onLoad() {
      this.stylelist = { ...this.stylelist,
        pageNum: this.index
      };
      this.loading = true;
      this.axios.get("https://apis.netstart.cn/bcomic/ClassPage", {
        params: { ...this.stylelist,
          pageNum: this.index
        }
      }).then(res => {
        this.loading = false;
        this.list.push(...res.data.data);
        this.index++;
      });
    }

  }
};