export default {
  data: function () {
    return {
      webpage: [],
      appmenu: []
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/UCenterConf").then(res => {
      let minemenu = res.data.data.confs;
      this.webpage = minemenu.slice(0, 4);
      this.appmenu = minemenu.slice(4, 12);
    });
  }

};