import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
export default {
  data: function () {
    return {
      textvalue: "",
      comic: null,
      value: 0,
      resivewtext: "你觉得这部漫画怎么样呢？分享一下阅读后的感受吧~",
      discussobj: JSON.parse(localStorage.getItem("textvalue")) || []
    };
  },
  watch: {
    value: function (newvalue) {
      if (newvalue == 0) {
        this.resivewtext = "你觉得这部漫画怎么样呢？分享一下阅读后的感受吧~";
      } else if (newvalue == 1 || newvalue == 2 || newvalue == 3) {
        this.resivewtext = "分享一下这部漫画中，你喜欢和不喜欢的部分吧，可以给小伙伴做参考哦~";
      } else if (newvalue == 4 || newvalue == 5) {
        this.resivewtext = "分享一下为什么喜欢这部漫画吧，可以安利更多的小伙伴来追漫哦~";
      }
    }
  },

  created() {
    let mydiscussviewmsg = this.discussobj.filter(o => o.comicid == this.$route.query.id)[0];
    this.textvalue = mydiscussviewmsg ? mydiscussviewmsg.content : "";
    this.value = mydiscussviewmsg ? mydiscussviewmsg.score : 0;
    this.axios.get("https://apis.netstart.cn/bcomic/ComicDetail", {
      params: {
        comicId: this.$route.query.id
      }
    }).then(res => {
      this.comic = res.data.data;
    });
  },

  methods: {
    getdiscuss: function () {
      if (this.value < 1) {
        Toast({
          message: '最少点评一星哦~',
          position: 'bottom'
        });
      } else if (this.textvalue.length < 10) {
        Toast({
          message: '最少点评十个字哦~',
          position: 'bottom'
        });
      } else if (this.value >= 1 && this.textvalue.length >= 10) {
        let nowtime = new Date();
        let time = nowtime.getFullYear() + "-" + (nowtime.getMonth() + 1) + "-" + nowtime.getDate() + " " + nowtime.getHours() + ":" + nowtime.getMinutes();
        let timeid = nowtime.getTime();
        let discussob = {
          uid: 81188408,
          face: "http://i2.hdslb.com/bfs/face/16e027b5961b75cf6e5d9d2787f84273f881fcd6.jpg",
          nick_name: "米浴单推人",
          content: this.textvalue,
          id: timeid,
          like_count: 0,
          score_update_time: time,
          score: this.value * 2,
          comicid: this.$route.query.id
        };
        this.discussobj = [discussob, ...this.discussobj.filter(o => o.comicid != this.$route.query.id)];
        localStorage.setItem("textvalue", JSON.stringify(this.discussobj));
        this.$router.back();
      }
    }
  }
};