import Vue from "vue";
import { Lazyload } from "vant";
Vue.use(Lazyload);
export default {
  data: function () {
    return {
      GetClassPageHomeBanner: [],
      images: [],
      bgindex: 0
    };
  },
  props: {
    item: Object
  },
  methods: {
    onChange(index) {
      this.bgindex = index;
    }

  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageHomeBanner", {
      params: {
        id: this.item.id
      }
    }).then(res => {
      this.GetClassPageHomeBanner = res.data.data.banner;
      this.images = res.data.data.banner.map(o => o.image_url + "@660w.webp");
    });
  }

};