import "core-js/modules/es.array.push.js";
import UserZoneRevuewsCard from "../components/UserZoneRevuewsCard.vue";
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);
export default {
  data: function () {
    return {
      msg: null,
      active: 0,
      activec: 0,
      index: 1,
      indexr: 1,
      comiclist: [],
      reviewslist: [],
      loading: false,
      finished: false,
      loadingr: false,
      finishedr: false,
      backbg: false,
      followlist: JSON.parse(localStorage.getItem("followlist")) || []
    };
  },
  methods: {
    editmsg: function () {
      Toast('内容整改中~');
    },
    setfollow: function () {
      this.followlist = [{
        id: this.$route.query.id,
        usermsg: this.msg
      }, ...this.followlist.filter(o => o.id != this.$route.query.id)];
      localStorage.setItem("followlist", JSON.stringify(this.followlist));
    },
    removefollow: function () {
      this.followlist = [...this.followlist.filter(o => o.id != this.$route.query.id)];
      localStorage.setItem("followlist", JSON.stringify(this.followlist));
    },

    changebg() {
      if (this.$refs.b.scrollTop > 150) {
        this.backbg = true;
      } else if (this.$refs.b.scrollTop <= 150) {
        this.backbg = false;
      }
    },

    onLoad() {
      if (this.activec == 0) {
        this.loadingr = true;
        this.axios.get("https://apis.netstart.cn/bcomic/GetReviews", {
          params: {
            uid: this.$route.query.id,
            pageSize: 15,
            pageNum: ++this.indexr
          }
        }).then(res => {
          this.loadingr = false;

          if (res.data.data.reviews?.length < 15) {
            this.finishedr = true;
          }

          this.comiclist.push(...res.data.data?.fav_comics);
        });
      } else {
        this.loading = true;
        this.axios.get("https://apis.netstart.cn/bcomic/GetFavComics", {
          params: {
            uid: this.$route.query.id,
            pageSize: 15,
            pageNum: ++this.index
          }
        }).then(res => {
          this.loading = false;

          if (res.data.data?.fav_comics?.length < 15) {
            this.finished = true;
          }

          this.comiclist.push(...res.data.data?.fav_comics);
        });
      }
    }

  },

  created() {
    // console.log(this.followlist.some(o => o.id == this.$route.query.id));
    this.axios.get("https://apis.netstart.cn/bcomic/GetBaseSpaceInfo", {
      params: {
        uid: this.$route.query.id
      }
    }).then(res => {
      this.msg = res.data.data;
    });
    this.axios.get("https://apis.netstart.cn/bcomic/GetFavComics", {
      params: {
        uid: this.$route.query.id,
        pageSize: 15,
        pageNum: 1
      }
    }).then(res => {
      if (res.data.data?.reviews?.length < 15) {
        this.finished = true;
      }

      this.comiclist = res.data.data?.fav_comics;
    });
    this.axios.get("https://apis.netstart.cn/bcomic/GetReviews", {
      params: {
        uid: this.$route.query.id,
        pageSize: 15,
        pageNum: 1
      }
    }).then(res => {
      if (res.data.data?.reviews.length < 15) {
        this.finishedr = true;
      }

      this.reviewslist = res.data.data?.reviews;
    });
  },

  components: {
    UserZoneRevuewsCard
  }
};