import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import TheStartView from "../views/TheStartView.vue";
import BookShelfView from "../views/BookShelfView.vue";
import BookShelfViewVisited from "../views/BookShelfViewVisited.vue";
import MineView from "../views/MineView.vue";
import MineViewVisited from "../views/MineViewVisited.vue";
import MallView from "../views/MallView.vue";
import ComicReadPage from "../views/ComicReadPage.vue";
import ComicView from "../views/ComicView.vue";
import TypeView from "../views/TypeView.vue";
import SearchView from "../views/SearchView.vue";
import FeerView from "../views/FeerView.vue";
import CommenView from "../views/CommenView.vue";
import AllType3view from "../views/AllType3view.vue";
import ShopView from "../views/ShopView.vue";
import CommentView from "../views/CommentView.vue";
import DiscussView from "../views/DiscussView.vue";
import DiscussDerailsView from "../views/DiscussDerailsView.vue";
import WebView from "../views/WebView.vue";
import TabBar from "../components/TabBar.vue";
import DrawStarView from "../views/DrawStarView.vue";
import UserZoneView from "../views/UserZoneView.vue";
import BilibiliLogin from "../views/BilibiliLogin.vue";
import SettingView from "../views/SettingView.vue";
import MyDiscussView from "../views/MyDiscussView.vue";
import MyFollowView from "../views/MyFollowView.vue";
import ReportView from "../views/ReportView.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'thestartview',
    component: TheStartView
  },
  {
    path: '/home',
    name: 'home',
    component: TabBar,
    children: [
      {
        path: "/",
        component: HomeView,
      },
      {
        path: "/mallview",
        component: MallView,
      },
      {
        path: "/bookshelfview",
        name: 'bookshelfview',
        component: BookShelfView,
        beforeEnter: (to, from, next) => {
          to, from, next
          let token = JSON.parse(localStorage.getItem("token")) || 0
          if (token != "13100435060") {
            next({
              path: '/bookshelfviewvisited'
            })
          } else if (token == "13100435060") {
            next()
          }
        }
      },
      {
        path: "/bookshelfviewvisited",
        name: 'bookshelfviewvisited',
        component: BookShelfViewVisited,
        beforeEnter: (to, from, next) => {
          to, from, next
          let token = JSON.parse(localStorage.getItem("token")) || 0
          if (token != "13100435060") {
            next()
          } else if (token == "13100435060") {
            next({
              path: '/bookshelfview'
            })
          }
        }
      },
      {
        path: "/mineview",
        name: 'mineview',
        component: MineView,
        beforeEnter: (to, from, next) => {
          to, from, next
          let token = JSON.parse(localStorage.getItem("token")) || 0
          if (token != "13100435060") {
            next({
              path: '/mineviewvisited'
            })
          } else if (token == "13100435060") {
            next()
          }
        }
      },
      {
        path: "/mineviewvisited",
        name: 'mineviewvisited',
        component: MineViewVisited,
        beforeEnter: (to, from, next) => {
          to, from, next
          let token = JSON.parse(localStorage.getItem("token")) || 0
          if (token != "13100435060") {
            next()
          } else if (token == "13100435060") {
            next({
              path: '/mineview'
            })
          }
        }
      },
    ],
  },
  {
    path: '/comicreadpage',
    name: 'comicreadpage',
    component: ComicReadPage
  },
  {
    path: '/comicview',
    name: 'comicview',
    component: ComicView
  },
  {
    path: '/typeview',
    name: 'typeview',
    component: TypeView
  },
  {
    path: '/searchview',
    name: 'searchview',
    component: SearchView
  },
  {
    path: '/feerview',
    name: 'feerview',
    component: FeerView
  },
  {
    path: '/commenview',
    name: 'commenview',
    component: CommenView
  },
  {
    path: '/alltype3view',
    name: 'allTtpe3view',
    component: AllType3view
  },
  {
    path: '/shopview',
    name: 'shopview',
    component: ShopView
  },
  {
    path: '/commentview',
    name: 'commentview',
    component: CommentView
  },
  {
    path: '/discussview',
    name: 'discussview',
    component: DiscussView
  },
  {
    path: '/discussderailsview',
    name: 'discussderailsview',
    component: DiscussDerailsView
  },
  {
    path: '/webview',
    name: 'webview',
    component: WebView
  },
  {
    path: '/drawstarview',
    name: 'drawstarview',
    component: DrawStarView
  },
  {
    path: '/userzoneview',
    name: 'userzoneview',
    component: UserZoneView
  },
  {
    path: '/settingview',
    name: 'settingview',
    component: SettingView
  }, 
  {
    path: '/mydiscussview',
    name: 'mydiscussview',
    component: MyDiscussView
  },
  {
    path: '/myfollowview',
    name: 'myfollowview',
    component: MyFollowView
  },
  {
    path: '/reportview',
    name: 'reportview',
    component: ReportView
  },
  {
    path: '/bilibililogin',
    name: 'bilibililogin',
    component: BilibiliLogin,
    beforeEnter: (to, from, next) => {
      to, from, next
      let token = JSON.parse(localStorage.getItem("token")) || 0
      let fromname = from.name?.split("visited")[0] || "home"
      localStorage.setItem("fromname", JSON.stringify(fromname));
      if (token != "13100435060") {
        next()
      } else if (token == "13100435060") {
        next({
          path: '/home'
        })
      }
    }
  },
]

const router = new VueRouter({
  routes,
})



const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

export default router
