      if (this.value == "13100435060" && this.password == "13100435060") {
        localStorage.setItem("token", JSON.stringify(13100435060));
        this.settokenbol = true;
        this.msg = "/" + JSON.parse(localStorage.getItem("fromname")) || "home";
      } else {
        this.settokenbol = false;
