import "core-js/modules/es.array.push.js";
import { Toast } from 'vant';
import { mapMutations, mapState } from "vuex";
export default {
  methods: { ...mapMutations(["setlikere", "removelikere", "setunlikere", "removeunlikere"]),

    onSelect(option) {
      if (option.name == "复制链接") {
        Toast("复制成功");
        navigator.clipboard.writeText(window.location.href);
        this.showShare = false;
      } else {
        Toast("正在拉起" + option.name + "...");
        this.showShare = false;
      }
    },

    openpage: function () {
      this.$router.push({
        path: '/reportview'
      });
    }
  },
  props: {
    item: Object
  },
  computed: { ...mapState(["likelistre", "unlikelistre"])
  },
  data: function () {
    return {
      actions: [{
        name: '举报'
      }],
      show: false,
      showShare: false,
      options: [{
        name: '微信',
        icon: 'wechat'
      }, {
        name: '朋友圈',
        icon: 'wechat-moments'
      }, {
        name: '微博',
        icon: 'weibo'
      }, {
        name: 'QQ',
        icon: 'qq'
      }, {
        name: '复制链接',
        icon: 'link'
      }, {
        name: '分享海报',
        icon: 'poster'
      }, {
        name: '二维码',
        icon: 'qrcode'
      }, {
        name: '小程序码',
        icon: 'weapp-qrcode'
      }],
      created: null,
      ranklist: [require("@/assets/resviews/club_v1.png"), require("@/assets/resviews/club_v2.png"), require("@/assets/resviews/club_v3.png"), require("@/assets/resviews/club_v4.png"), require("@/assets/resviews/club_v5.png"), require("@/assets/resviews/club_v6.png")]
    };
  },

  created() {
    let time = new Date().getTime();
    let timecu = time - this.item?.reply_control?.time_desc.match(/\d+(\.\d+)?/g)[0] * 86400000;
    let creattimeget = new Date(timecu);
    let creattime = creattimeget.getFullYear() + "-" + (creattimeget.getMonth() + 1) + "-" + creattimeget.getDate();
    this.creattime = creattime; // console.log(this.item.replies.content.message);
  }

};