export default {
  data() {
    return {
      show: false,
      followlist: JSON.parse(localStorage.getItem("followlist")) || [],
      chooseid: null
    };
  },

  methods: {
    showPopup(id) {
      this.show = true;
      this.chooseid = id;
    },

    removefollow: function () {
      this.show = false;
      this.followlist = [...this.followlist.filter(o => o.id != this.chooseid)];
      localStorage.setItem("followlist", JSON.stringify(this.followlist));
    }
  }
};