export default {
  data: function () {
    return {
      comics: [],
      index: 1
    };
  },
  methods: {
    updatemsg: function () {
      this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageSixComics", {
        params: {
          id: this.item.id,
          pageNum: ++this.index,
          pageSize: 6,
          isAll: 1
        }
      }).then(res => {
        this.comics = res.data.data.roll_six_comics;
      });
    }
  },
  props: {
    item: Object
  },

  created() {
    this.axios.get("https://apis.netstart.cn/bcomic/GetClassPageSixComics", {
      params: {
        id: this.item.id,
        pageNum: 1,
        pageSize: 6,
        isAll: 1
      }
    }).then(res => {
      this.comics = res.data.data.roll_six_comics;
    });
  }

};